import React from "react";
import styled, { css } from "styled-components";
import { lineLimit } from "../utils/cssLineLimit";

export const flex = {
  row: {
    display: "flex",
    flexDirection: "row",
  } as const satisfies React.CSSProperties,
  column: {
    display: "flex",
    flexDirection: "column",
  } as const satisfies React.CSSProperties,
  center: {
    alignItems: "center",
    justifyContent: "center",
  } as const satisfies React.CSSProperties,
} as const;

const row = css`
  display: flex;
  flex-direction: row;
`;
const column = css`
  display: flex;
  flex-direction: column;
`;
const center = css`
  align-items: center;
  justify-content: center;
`;
const between = css`
  align-items: center;
  justify-content: space-between;
`;
export const flexCSS = { row, column, center, between } as const;
export const absoluteFill = {
  position: "absolute",
  left: 0,
  top: 0,
  right: 0,
  bottom: 0,
  width: "100%",
  height: "100%",
} as const satisfies React.CSSProperties;

export const absoluteFillCSS = css`
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
`;
export const minDesktopFullSize = {
  width: "100vw",
  height: "100vh",
  minWidth: 800,
  minHeight: 600,
} as const satisfies React.CSSProperties;
export const minDesktopFullSizeCSS = `
  width: 100vw;
  height: 100vh;
  min-width: 800px;
  min-height: 600px;
`;
export const ellipsis = css`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;
export const scrollbar = css`
  position: relative;

  &::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    right: 5px;
    bottom: 5px;
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 12px;
    background: transparent;
    transition: all 0.2s ease;
  }

  &:hover {
    &::-webkit-scrollbar-thumb {
      background: #d9d9d9;
    }
  }
`;
export const commons = {
  flex,
  flexCSS,
  absoluteFill,
  absoluteFillCSS,
  minDesktopFullSize,
  minDesktopFullSizeCSS,
  ellipsis,
  ellipsisLineTwo: lineLimit(),
  ellipsisLineThree: lineLimit(3),
  ellipsisLineFour: lineLimit(4),
  scrollbar,
} as const;

export const FlexBlank = styled.div`
  flex: 1;
`;
